import * as path from 'path';

import {
  BaseGenerator,
  AvailableFrameworks
} from '../../common/BaseGenerator';
import * as Utils from '../../common/utilities';

import * as BaseComponent from '../component/BaseComponentGenerator';

export interface IWebpartOptions extends BaseComponent.IBaseComponentOptions {}

export interface IWebpartContext extends BaseComponent.IBaseComponentContext {}

// tslint:disable-next-line:no-any
export function composeWith(base: BaseGenerator<any, any>, options: IWebpartOptions): void {
  Utils.compose(path.basename(__dirname), base, options);
}

// tslint:disable-next-line:no-any
export function defineOptions(generator: BaseGenerator<any, any>): void {
  BaseComponent.defineOptions(generator, 'Web part');
}

export class WebpartGenerator extends BaseComponent.BaseComponentGenerator<IWebpartOptions, IWebpartContext> {
  protected readonly friendlyName: string = 'Web part';
  protected readonly codeName: string = 'WebPart';
  protected readonly allowedFrameworks: AvailableFrameworks[] = ['knockout', 'react'];
  protected readonly folderName: string = 'webparts';

  public initializing(): void { /* no-op */ }

  public prompting(): Promise<void> {
    return super.prompting();
  }

  public configuring(): void {
    super.configuring();
    this.ensureDependencyGroup('webpart');
    this.serveJson.setUpForWebParts();
  }

  public writing(): void {
    super.writing(true);
  }

  public install(): void {
    super.install();
  }

  public end(): void { /* no-op */}

  // tslint:disable-next-line:no-any
  protected constructor(args: string | string[], options: any) {
    super(args, options);
    defineOptions(this);
  }
}

// Yeoman expects exports of this type, which overrides the TS exports above
// Ensure that anything marked as export above is also listed here
module.exports = WebpartGenerator;
module.exports.defineOptions = defineOptions;
module.exports.composeWith = composeWith;
